export default {
  data() {
    return {
      // 性别数据
      gender: [
        {
          id: 0,
          text: "先生",
          value: "0"
        },
        {
          id: 1,
          text: "女士",
          value: "1"
        }
      ],
      // 控制被选中性别的那一项
      current: null,
      // 地址选择
      address: '选择详细地址'
    };
  },
  methods: {
    // 去确认订单页面
    goConfirmOrder() {
      uni.navigateTo({
        url: "/pages/confirmOrder/confirmOrder"
      });
    },
    // 当选择性别发生改变的时候
    radioChange(e) {
      this.current = e.target.value;
    },
    bindPickerChange(e) {
      this.address = e.target.value
    }
  }
};
